#shopping-basket{
  position: relative;
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
#shopping-basket:hover{
  cursor: pointer;
  transform: scale(1.12);
  transition: 0.4s;
}

#shopping-basket > i {
  font-size: 42px;
  padding-top:5px ;
  color: white;
}

#shopping-basket > i > span {
  position: absolute;
  top:4px;
  right: 0;
  font-family: 'Baumans', cursive;
  font-size: 15px;
  font-weight: bold;
  color: white;
  background-color: tomato;
  box-sizing: border-box;
  padding: 1px 4px;
  border-radius: 4px;
}