#container {
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

#my-nav-bar{
  background-image: linear-gradient(15deg, seagreen 0%, teal 100%);
  display: grid;
  grid-template-columns: 15% 70% 15%;
  height: 100px;
  margin-bottom: 30px;
  box-sizing: border-box;
  padding: 0 20px;
  box-shadow: 2px 0px 5px ;
}

#logo-container{
  display: flex;
  align-items: center;
  height: 100%;
  box-sizing: border-box;
}

#logo-container > a {
  color: gold;
  text-decoration: none;
  box-sizing: border-box;
  font-family: 'Indie Flower', cursive;
  font-size: 30px;
  font-weight: bold;
  text-shadow: 1px 1px 2px red;
}
  
#logo-container > a:hover{
  transform:scale(1.05);
  transition: 0.5s;
}

.navbar-middle-section{
  display: flex;
  justify-content: center;
}


.my-nav-list{
  display: flex;
  width: 100%;
  height: 100%;
  list-style-type: none;
  justify-content: center;
  align-items: center; 
}

.my-nav-list li{
  box-sizing: border-box;
  margin-right: 60px;
}
.my-nav-list li:last-of-type{
  margin-right: 0;
}

.my-nav-list li a{
  color: white;
  text-decoration: none;
  box-sizing: border-box;
  font-family: 'Exo', sans-serif;
  font-size: 22px;
}

#basketContainer{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

ul > li:hover {
  transform:scale(1.05);
  transition: 0.5s;
  text-shadow: 1px 1px 2px red;
}

.my-nav-list li a:hover{
  color: gold;
}

main {
  margin-bottom: 200px;
}

.fa-home{
  font-size: 40px;
}
.fa-home:hover{
  transform:scale(1.1);
  transition: 0.5s;
}

#my-nav-bar2{
  background-image: linear-gradient(15deg, seagreen 0%, teal 100%);
  box-sizing: border-box;
  height: 60px;
  display: none;
  width: 100%;
  border: none;
}

.fa-bars{
  color: white;
  font-size: 35px;
  padding: 10px;
}
#responsiv-ul{
  background-image: linear-gradient(90deg, seagreen 0%, teal 100%);
  text-align: center;
  list-style: none;
  margin: 0;
  margin-bottom: 30px;
  display: none;
  padding: 0;
  box-sizing: border-box;

}

#responsiv-ul li{
  box-sizing: border-box;
  position: relative;
  
}
#responsiv-ul li:hover{
  background-color: tomato;
  transform: none;
}

#responsiv-ul a{
  text-decoration: none;
  color: white;
  padding: 0;
  font-weight: bold;
  font-size: 1.5em;
  display: flex;
  height: 100%;
  padding: 20px 0;
  justify-content: center;
}

#responsiv_basket{
  color: white;
  font-size: 35px;
}

@media (max-width: 770px) {

  #my-nav-bar{
    display: none;
  }

  #my-nav-bar2{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    position: fixed;
    z-index: 1000;
  }

  #responsiv-ul{
    position: fixed;
    top: 58px;
    width: 100%;
    z-index: 999;
  }

  main{
    padding-top: 30px;
  }

}