.cart-main-container{
  width: 80%;
  margin: 40px auto;
}

.cart-item-box{
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 25% 50% 25%;
  padding: 10px;
  margin: 10px 10px 30px 10px;
  border-radius: 10px;
  box-shadow: 2px 2px 4px gray;
}

.cart-item-box-title li {
  list-style-type: none;
  display: inline;
  margin-right:20px ;
}

.cart-item-box-title li:hover {
  cursor: text;
  font-weight: normal;
}

.cart-item-box > img {
  width: 100px
}

.cart-item-box-btn{
  display: flex;
  justify-content: center;
  align-items: center;
}

#err-txt{
  text-align: center;
  font-weight: bold;
  color: red;
}

@media (max-width: 770px) {
  .cart-main-container{
    width: 100%;
    text-align: center;
  }

  .cart-item-box{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 5px;
    margin: 10px 10px 30px 10px;
    position: relative;
    border: solid black 2px;
  }

  .cart-item-box-btn  span{
    display: none;
  }


  .cart-item-box-btn{
    position: absolute;
    right: 8px;
    top: 25px;
  }

  .fa-trash{
    padding: 0;
  }

  #cart-details-table{
    width: 100%;
  }

}