.singleProductContainer{
  width: 80%;
  margin: 0 auto;
}


.block-1{
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-column-gap: 20px;
  margin: 40px 0;
  margin-bottom: 120px;
}

#description-box{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.add-to-basket{
  display: flex;
  margin-top: 20px;
}

.add-to-basket > *{
  height: 100%;
  margin: 5px;
}

.add-to-basket> input{
  width: 100px;
  margin-right: 10px;
}

#add-to-basket-btn{
  border: none;
  background-color: steelblue;
  display: flex;
  align-items: center;
  color: white;
}

#add-to-basket-btn:hover{
  background-color: royalblue;
}

table > tr > td{
  box-sizing: border-box;
  width: 350px;
  padding: 10px;
  
}

#cart-details-table td{
  box-sizing: border-box;
  width: 200px;
  padding: 10px;
}

tr:nth-child(odd){
  background-color: #6495ed50;
}

table{
  border-top: 3px solid black ;
  font-family: 'Baumans', cursive;
  font-size: 18px;
}

@media (max-width: 770px) {
  .singleProductContainer{
    width: 100%;
    margin: 0 auto;
    padding-top: 70px;
  }

  .singleProductContainer * {
    box-sizing: border-box;
  }

  .block-1{
  grid-template-columns: 100%; 
  padding: 0; 
  grid-template-rows: 40% 60%;
  box-sizing: border-box;
  margin: 0 auto;
  }

  #img_ID{
    display: flex;
    justify-content: center;
  }

  .block-1 img{
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    padding: 10px 20px;
  }

  #description-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 10px 20px;
  }
  #description-box > p{
    box-sizing: border-box;
    text-align: left;
    padding: 0 5px;
    font-size: 1.3em;
  }

  .add-to-basket{
    display: flex;
    justify-content: center;
  }

  .block-2{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 70px;
  }

  table{
    text-align: left;
  }

  table > tr > td{
    padding: 5px 20px;
    font-size: 1.3em;
  }
  
