#title-search-section{
  display: flex;
  width: 75%;
  margin: 0 auto;
  justify-content: space-between;
  font-family: 'Exo', sans-serif;

}

#title-search-section > input {
  width: 400px;
}

.datorer-container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  box-sizing: border-box;
  width: 90%;
  margin: 0 auto;
}

.datorer-container > *{
  padding: 10px;
  margin: 20px auto;
  width: 300px;
  position: relative;
  text-align: center;
  box-sizing: border-box;
 }

 .datorer-container h4{
   white-space: nowrap;
   overflow: hidden;
   text-overflow: ellipsis;
 }

 .datorer-container  h4:hover{
  cursor: pointer;
  overflow: visible;
}

.datorer-container img {
  width: 200px;
  margin: 0 auto;
}

#btnInfo{
  text-decoration: none;
  display: block;
  font-weight: bold;
  color: snow;
  background-color: steelblue;
  padding: 5px 20px;
  margin: 0 auto;
  margin-top: 30px;
  border: none;
  width: 70%;
}

#btnInfo:hover{
  background-color: teal;
  transform: scale(1.03);
  transition: 0.2s;
}

article{
  position: relative;
  width: 70px;
  height: 70px;
  background-color: cadetblue;
  transition: 4s;
  overflow: hidden;
  word-wrap: nowrap;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: aliceblue;
  margin: 0 auto;
}


@media (max-width: 770px) {
  #main_template{
    display: flex;
    flex-direction: column;
  }

  #title-search-section{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0;
    text-align: center;
    padding-top: 50px;
  }

  #title-search-section > input {
    width: 95%;
    margin: 0 auto;
  }

  .datorer-container{
    display: block;
    box-sizing: border-box;
    width: 100%;
    margin: 0 auto;
  }

  .datorer-container > *{
    width: 100%;
   }


   .datorer-container img {
    width: 60%;
    margin: 0 auto;
  }
}